@import 'ScssHelpers/helpers-all';
.popup {
	position: fixed;
	top: 0;
	left: 0;
	z-index: -1;
	display: flex;
	align-content: stretch;
	width: 100%;
	height: 100%;
	max-height: 100vh;
	overflow-x: hidden;
	overflow-y: auto;
	padding: 2rem;
	background-color: rgba($black, 0.96);
	transition: opacity 0.3s ease;
	will-change: opacity;
	&:not(.popup--open_state) {
		opacity: 0;
		pointer-events: none;
	}
	&.popup--open_state {
		z-index: z-index(popup);
		opacity: 1;
		pointer-events: initial;
	}
}

.popup_in {
	position: relative;
	width: $page_width;
	max-width: 100%;
	margin: auto;
	background-color: $white;
}

.popup_close {
	position: absolute;
	top: 1rem;
	right: 1rem;
	z-index: 5;
	width: 3.4rem;
	height: 3.4rem;
	transition: opacity 0.3s ease;
	will-change: opacity;
	cursor: pointer;
	&:hover,
	&:focus {
		opacity: 0.8;
	}
	&::before,
	&::after {
		position: absolute;
		top: 1.5rem;
		left: 0;
		height: 2px;
		width: 3.4rem;
		content: '';
		background-color: #fff;
		.popup_close:hover & {
		}
	}
	&::before {
		transform: rotate(45deg);
	}
	&::after {
		transform: rotate(-45deg);
	}
}
