:root {
  --full-screen: calc(var(--vh, 1vh) * 100);
  --height-footer: 6.6rem;
  --gl-indent: 3.2rem;
}
@media screen and (min-width: 1024px) {
  :root {
    --height-header: 10rem;
  }
}
@media screen and (max-width: 1023.5px) {
  :root {
    --height-header: 5.4rem;
  }
}
:root ::-webkit-scrollbar {
  position: absolute;
  width: 0.6rem;
}
:root ::-webkit-scrollbar-track {
  background-color: rgba(255, 255, 255, 0.4);
}
:root ::-webkit-scrollbar-thumb {
  background-color: #aaa;
  border-radius: 0.4rem;
}

html {
  height: 100%;
  font: 400 0.6944444444vw/1.33 "Bebas", sans-serif;
}
@media screen and (max-width: 1023.5px) {
  html {
    font-size: 10px;
  }
}
@media screen and (max-width: 390px) {
  html {
    font-size: 2.5641025641vw;
  }
}

body {
  position: relative;
  display: flex;
  height: 100%;
  font-size: 1.6rem;
  color: #000;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fff;
  overflow-x: hidden;
  transition: opacity 0.3s;
}
@supports (display: grid) {
  body {
    display: block;
  }
}
body.body--loaded_mod {
  opacity: 1;
}
body.body--dark_mod {
  background-color: #141313;
}
body.body--animating_mod, body.body--open_menu_state {
  overflow: hidden;
}
body.body--scrolled_mod {
  pointer-events: none;
}

body.body--popup_open {
  overflow: hidden;
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #fff;
  opacity: 1;
  transition: opacity 0.3s;
  z-index: 1000;
}
.body--loaded .preloader {
  pointer-events: none;
  opacity: 0;
}

.global_wrapper {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  height: auto !important;
  overflow-x: hidden;
}

.wrapper {
  display: flex;
  flex-direction: column;
  flex: 1 0 auto;
}

.base {
  width: 100%;
  position: relative;
  padding: 0;
  flex: 1 0 auto;
}

a {
  text-decoration: none;
  color: #4aa3df;
}
a:hover, a:focus {
  text-decoration: underline;
}

.editor-styles-wrapper .wp-block {
  margin-bottom: 5rem;
}

html :where(.wp-block) {
  max-width: 1200px;
}

.editor-styles-wrapper {
  overflow-x: hidden;
}

.anim_wrap {
  height: 100vh;
  overflow: hidden;
}

.power,
.extracted,
.list {
  visibility: hidden;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  height: var(--height-header);
  width: 100%;
  padding: 1rem var(--gl-indent);
  font-size: 1rem;
  will-change: transform;
  transition: transform 0.3s, background-color 0.3s;
  z-index: 100;
}
.header.header--pos_state {
  transform: translateY(-100%);
}
.header.header--scroll_state {
  background-color: #fff;
}
.body--dark_mod .header.header--scroll_state {
  background-color: #000;
}
.body--dark_mod .header {
  background-color: transparent;
}

.header_in {
  display: flex;
  align-items: center;
  width: 100%;
  margin: 0 auto;
}

.header__controls {
  display: flex;
  align-items: center;
  margin-left: auto;
}

.header__btn {
  margin-top: 0.4rem;
}
@media screen and (min-width: 1024px) {
  .header__btn:not(:last-child) {
    margin-right: 3rem;
  }
}
@media screen and (max-width: 1023.5px) {
  .header__btn:not(:last-child) {
    margin-right: 1.6rem;
  }
}

.header__logo {
  position: relative;
  z-index: 1001;
  display: block;
  width: 8.2rem;
  height: 3.6rem;
}

.header__logo_img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.header--scroll_state .header__logo_text {
  fill: #000;
}
.body--dark_mod .header__logo_text {
  fill: #fff;
}
@media screen and (max-width: 1023.5px) {
  .body--open_menu_state .header__logo_text {
    fill: #000;
  }
}

@media screen and (max-width: 1023.5px) {
  .body--open_menu_state .header__logo_bg {
    fill: #333333;
  }
}

@media screen and (max-width: 1023.5px) {
  .body--open_menu_state .header__logo_letter {
    fill: #fff;
  }
}

.header__trigger {
  position: relative;
  z-index: 1001;
  margin: 0.4rem 0 0 auto;
  font-size: 1.6rem;
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  color: #000;
  transition: color 0.3s;
  cursor: pointer;
}
@media screen and (min-width: 1024px) {
  .header__trigger {
    padding-right: 5rem;
  }
}
@media screen and (max-width: 1023.5px) {
  .header__trigger {
    padding-right: 4.4rem;
  }
}
.header--scroll_state .header__trigger {
  color: #000;
}
.body--dark_mod .header__trigger {
  color: #fff;
}
.header__trigger:hover {
  color: #333333;
}
.body--dark_mod .header__trigger:hover {
  color: #dce161;
}
.body--open_menu_state .header__trigger {
  color: #000;
}
.body--open_menu_state .header__trigger:hover, .body--open_menu_state .header__trigger:focus {
  color: #000;
}
.header__trigger:before, .header__trigger:after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  border-radius: 0.4rem;
  background-color: currentColor;
}
.header__trigger:before {
  height: 0.4rem;
  margin-top: -0.2rem;
}
@media screen and (min-width: 1024px) {
  .header__trigger:before {
    width: 3.6rem;
  }
}
@media screen and (max-width: 1023.5px) {
  .header__trigger:before {
    width: 3rem;
  }
}
.header__trigger:after {
  width: 0.4rem;
  transition: opacity 0.3s;
}
@media screen and (min-width: 1024px) {
  .header__trigger:after {
    height: 3.6rem;
    margin-top: -1.8rem;
    margin-right: 1.6rem;
  }
}
@media screen and (max-width: 1023.5px) {
  .header__trigger:after {
    height: 3rem;
    margin-top: -1.5rem;
    margin-right: 1.4rem;
  }
}
.body--open_menu_state .header__trigger:after {
  opacity: 0;
}

.header_menu {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  height: var(--full-screen);
  background-color: #dce161;
  overflow-y: auto;
  opacity: 0;
  pointer-events: none;
  transform: translateX(100%);
  transition: all 0.3s;
}
@media screen and (min-width: 1024px) {
  .header_menu {
    width: 68.2rem;
    padding: min(14rem, 13vh) 5.4rem 6rem;
  }
}
@media screen and (max-width: 1023.5px) {
  .header_menu {
    width: 100%;
    padding: 7.8rem 3.2rem 3rem;
  }
}
.body--open_menu_state .header_menu {
  opacity: 1;
  transform: translateX(0);
  pointer-events: initial;
}

@media screen and (min-width: 1024px) {
  .header_menu__list {
    margin-bottom: 4rem;
  }
}
@media screen and (max-width: 1023.5px) {
  .header_menu__list {
    margin-bottom: 9.4rem;
  }
}

.header_menu__item {
  display: flex;
  justify-content: center;
}
@media screen and (min-width: 1024px) {
  .header_menu__item:not(:last-child) {
    margin-bottom: 4rem;
  }
}
@media screen and (max-width: 1023.5px) {
  .header_menu__item:not(:last-child) {
    margin-bottom: 1.4rem;
  }
}

.header_menu__link {
  line-height: 1;
  font-weight: 400;
  display: block;
  padding: 1.2rem 1rem;
  line-height: 1;
  text-align: center;
  color: #222121;
  transition: all 0.3s;
}
@media screen and (min-width: 1024px) {
  .header_menu__link {
    font-size: 11.5rem;
  }
}
@media screen and (max-width: 1023.5px) {
  .header_menu__link {
    font-size: 6.4rem;
  }
}
@media screen and (min-width: 1024px) {
  .header_menu__link {
    min-width: 37rem;
  }
}
@media screen and (max-width: 1023.5px) {
  .header_menu__link {
    width: 100%;
  }
}
.header_menu__link:hover, .header_menu__link:focus {
  color: #dce161;
  background-color: #333333;
  text-decoration: none;
}

@media screen and (max-width: 1023.5px) {
  .header_menu__btn {
    display: flex;
    justify-content: center;
    margin-top: auto;
  }
}

@media screen and (min-width: 1024px) {
  .header_menu__contact {
    margin: auto 0 2.8rem;
  }
}
@media (max-width: 1023.5px) and (min-width: 768px) {
  .header_menu__contact {
    text-align: center;
  }
  .header_menu__contact:not(:last-child) {
    margin-bottom: 3rem;
  }
}

.header_menu__contact_title {
  font-size: 1.6rem;
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  color: #070707;
}

.header_menu__contact_link {
  font-size: 4rem;
  color: #000;
  opacity: 0.7;
  text-decoration: underline;
}

.section {
  position: relative;
  width: 100%;
  padding: 9.6rem var(--gl-indent);
  font-size: 1rem;
}

.section_in {
  max-width: 118.6rem;
  width: 100%;
  margin: 0 auto;
}

.section_wrap {
  will-change: transform;
}

.footer {
  width: 100%;
  min-height: var(--height-footer);
  flex: 0 0 auto;
  font-size: 1rem;
  border-top: 0.6rem solid #000;
  will-change: transform;
}
.body--dark_mod .footer {
  background-color: #fff;
}
@media screen and (min-width: 768px) {
  .footer {
    padding: 1.6rem var(--gl-indent) 2.2rem;
  }
}
@media screen and (max-width: 767px) {
  .footer {
    padding: 1.8rem var(--gl-indent) 3.8rem;
  }
}

.footer_in {
  width: 100%;
  max-width: 118.6rem;
  margin: 0 auto;
}

.footer_menu {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -4rem -1.8rem 0;
}

.footer_menu__item {
  padding: 0 4rem 1.8rem 0;
}
.footer_menu__item:nth-child(3) {
  margin-left: auto;
}
@media screen and (max-width: 767px) {
  .footer_menu__item {
    width: 100%;
  }
}

.footer_menu__link {
  position: relative;
  display: block;
  font-size: 1.6rem;
  font-weight: 700;
  font-family: "Raleway", sans-serif;
  color: #070707;
  text-transform: uppercase;
  transition: color 0.3s;
}
.footer_menu__link:hover, .footer_menu__link:focus {
  text-decoration: none;
  color: #dce161;
}
@media screen and (max-width: 767px) {
  .footer_menu__link {
    padding-left: 1.4rem;
  }
}
.footer_menu__link:before {
  content: "";
  position: absolute;
  top: 50%;
  border-radius: 50%;
  background-color: currentColor;
}
@media screen and (min-width: 1024px) {
  .footer_menu__link:before {
    width: 1rem;
    height: 1rem;
    margin-top: -0.6rem;
  }
}
@media screen and (max-width: 1023.5px) {
  .footer_menu__link:before {
    width: 0.9rem;
    height: 0.9rem;
    margin-top: -0.5rem;
  }
}
@media screen and (min-width: 768px) {
  .footer_menu__link:before {
    left: -1.4rem;
  }
}
@media screen and (max-width: 767px) {
  .footer_menu__link:before {
    left: 0;
  }
}

.hero {
  padding: 9.6rem var(--gl-indent) 0;
}
@media screen and (max-width: 1023.5px) {
  .hero {
    display: flex;
    min-height: 100vh;
  }
}
@media screen and (max-width: 1023.5px) {
  .hero .section_in {
    display: flex;
    flex-direction: column;
  }
}

.hero__title {
  line-height: 1;
  font-weight: 400;
  position: relative;
  max-width: 80rem;
  margin-bottom: 2.4rem;
}
@media screen and (min-width: 1024px) {
  .hero__title {
    font-size: 11.5rem;
  }
}
@media screen and (max-width: 1023.5px) {
  .hero__title {
    font-size: 6.4rem;
  }
}
@media screen and (max-width: 767px) {
  .hero__title {
    padding-right: 3rem;
  }
}

.hero__title_icon {
  position: absolute;
  top: 0;
  right: 100%;
  width: 5.6rem;
  height: 4rem;
  margin: 3rem 1.4rem 0 0;
}
@media screen and (max-width: 1023.5px) {
  .hero__title_icon {
    display: none;
  }
}

.hero__content {
  position: relative;
}
@media screen and (max-width: 1023.5px) {
  .hero__content {
    flex-grow: 1;
  }
}

.hero__text {
  display: flex;
  position: relative;
  z-index: 3;
  padding-top: 1.8rem;
}
.hero__text:after {
  content: "";
  flex-grow: 1;
  margin-left: -1px;
  background-color: #fff;
}

.hero__text_bg {
  position: absolute;
  background-color: #fff;
}
.hero__text_bg:nth-child(1) {
  top: 0;
  left: 1px;
  width: 50vw;
  height: calc(100% + 1px);
  margin-left: -50vw;
}
.hero__text_bg:nth-child(2) {
  top: 0;
  left: calc(100% - 1px);
  width: 50vw;
  height: 100%;
