@mixin st_btn {
	display: inline-flex;
	justify-content: center;
	align-items: center;

	text-align: center;
	white-space: nowrap;
	font-family: $raleway;

	cursor: pointer;

	&[type='submit'] {
		@include appearance(none);
	}

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

//color mixins:
@mixin btn_style_base {
	font-weight: 700;
	text-transform: uppercase;
	transition: background-color 0.3s ease;

	@include st_btn;

	@include bp($point_2, min) {
		color: $black_3;
		border: 0.6rem solid currentColor;
		background-color: transparent;
	}

	@include bp($point_3) {
		color: $yellow;
		background-color: $black_2;
	}

	&:hover {
		background-color: $black_3;
		border-color: $black_3;
		color: $yellow;
	}
}

@mixin btn_style_yellow {
	font-weight: 700;
	font-family: $raleway;
	background-color: $yellow;
	color: $black;
	transition: all 0.3s ease;

	@include st_btn;

	&:hover,
	&:focus {
		background-color: $black;
		color: $yellow;
	}
}

//size mixins:
@mixin btn_size_normal {
	padding: 0 1.2rem;

	@include bp($point_2, min) {
		height: 12.2rem;
		width: 100%;

		font-size: 5.2rem;
	}

	@include bp($point_3) {
		padding: 0 2.8rem;
		height: 4rem;

		font-size: 1.6rem;
	}
}

@mixin btn_size_xs {
	padding: 0 1.2rem;
	border-radius: 4rem;

	@include bp($point_2, min) {
		height: 1.8rem;
		width: 9.2rem;

		font-size: 0.71rem;
	}

	@include bp($point_3) {
		padding: 0 2.8rem;
		height: 3.7rem;
		width: 100%;

		font-size: 1.33rem;
	}
}

@mixin btn_size_md {
	@include bp($point_2, min) {
		height: 3.4rem;
		min-width: 10rem;
		padding: 0 1.8rem;

		font-size: 1.6rem;
	}

	@include bp($point_3) {
		padding: 0 1rem;
		height: 3rem;
		width: 100%;

		font-size: 1.4rem;
	}
}
