@import './universal/popup.scss';
@import '../helpers/helpers-all.scss';

.popup.sign_up_popup__wrapper {
	background-color: rgba(0, 0, 0, 0.78);

	.popup_in {
		padding: 2.4rem;
		width: 47.5rem;
		max-width: 100%;
		background-color: $white;
		border: 1px solid $black_6;

		.popup_close {
			top: 2rem;
			right: 2.1rem;
			width: 2.4rem;
			height: 2.4rem;

			&::before,
			&::after {
				content: '';
				width: 2rem;
				height: 2px;
				background-color: $black;
				border-radius: 2px;
			}
		}

		@include bp($point_5) {
			width: 100%;
			max-width: 32.6rem;
		}
	}
}

.sign_up_popup__header {
	margin-bottom: 2.2rem;
}

.sign_up_popup__title {
	font-family: $raleway;
	font-size: 2.4rem;
	font-weight: bold;
	line-height: normal;
}
