.footer {
	width: 100%;
	min-height: var(--height-footer);
	flex: 0 0 auto;

	font-size: 1rem;

	border-top: 0.6rem solid $black;
	will-change: transform;

	.body--dark_mod & {
		background-color: $white;
	}

	@include bp($point_4, min) {
		padding: 1.6rem var(--gl-indent) 2.2rem;
	}

	@include bp($point_4 - 1) {
		padding: 1.8rem var(--gl-indent) 3.8rem;
	}
}

.footer_in {
	width: 100%;
	max-width: $page_width;
	margin: 0 auto;
}

.footer_menu {
	display: flex;
	flex-wrap: wrap;

	margin: 0 -4rem -1.8rem 0;
}

.footer_menu__item {
	padding: 0 4rem 1.8rem 0;

	&:nth-child(3) {
		margin-left: auto;
	}

	@include bp($point_4 - 1) {
		width: 100%;
	}
}

.footer_menu__link {
	position: relative;

	display: block;

	font-size: 1.6rem;
	font-weight: 700;
	font-family: $raleway;
	color: $black_3;
	text-transform: uppercase;

	transition: color 0.3s;

	&:hover,
	&:focus {
		text-decoration: none;
		color: $yellow;
	}

	@include bp($point_4 - 1) {
		padding-left: 1.4rem;
	}

	&:before {
		content: '';

		position: absolute;
		top: 50%;

		border-radius: 50%;
		background-color: currentColor;

		@include bp($point_2, min) {
			width: 1rem;
			height: 1rem;
			margin-top: -0.6rem;
		}

		@include bp($point_3) {
			width: 0.9rem;
			height: 0.9rem;
			margin-top: -0.5rem;
		}

		@include bp($point_4, min) {
			left: -1.4rem;
		}

		@include bp($point_4 - 1) {
			left: 0;
		}
	}
}
