@import './universal/popup.scss';
@import '../helpers/helpers-all.scss';
@import '../ui/buttons.scss';

.popup_sign_up__list {
	&:not(:last-child) {
		margin-bottom: 2.5rem;
	}
}

.popup_sign_up__item {
	margin-bottom: 1.6rem;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.popup_sign_up__input_group {
	width: 100%;

	.popup_sign_up__label {
		margin-bottom: 0.1rem;
		font-family: $raleway;
		font-size: 1.6rem;
		font-weight: 700;
		color: $black_6;
	}

	.form_input__field {
		.form_input__element {
			width: 100%;
			border-radius: 3px;
			border: 2px solid $black_6;
			background: $white;
			font-family: $raleway;
			font-size: 1.4rem;
			color: $black;
			&::placeholder {
				font-family: $raleway;
				font-size: 1.4rem;
				font-weight: 400;
			}
		}
	}
}

.form_sign_up__action_buttons {
	display: flex;
	justify-content: flex-end;

}
