.btn_base {
	@include btn_style_base;
	@include btn_size_normal;
}

.form_sign_up__btn_close {
	margin-right: 1.2rem;
	background-color: none;
	font-family: $raleway;
	font-size: 1.6rem;
	font-weight: 700;
	color: $black_6;
	cursor: pointer;
	transition: color 0.3s;

	&:hover {
		color: $yellow;
	}
}

.form_sign_up__btn_sign_up {
	padding: 1rem;
	width: 17.2rem;
	border-radius: 5.8rem;
	background-color: $yellow;
	cursor: pointer;
	font-family: $raleway;
	font-size: 1.6rem;
	font-weight: 700;
	color: $black_6;
	transition: background-color 0.3s, color 0.3s;

	&:hover {
		background-color: $black;
		color: $yellow;
	}
}

.btn_v1 {
	@include btn_style_yellow;
	@include btn_size_xs;
}

.btn_v2 {
	@include btn_style_yellow;
	@include btn_size_md;
}
