@import 'family';
@import 'reset';
@import 'fonts';
@import 'print';
//------------------------------------------------------------base styles
:root {
	--full-screen: calc(var(--vh, 1vh) * 100);
	--height-footer: 6.6rem;
	--gl-indent: 3.2rem;
	@include bp($point_2, min) {
		--height-header: 10rem;
	}
	@include bp($point_3) {
		--height-header: 5.4rem;
	}

	::-webkit-scrollbar {
		position: absolute;
		width: 0.6rem;
	}
	::-webkit-scrollbar-track {
		background-color: rgba($white, 0.4);
	}
	::-webkit-scrollbar-thumb {
		background-color: #aaa;
		border-radius: 0.4rem;
	}
}

html {
	height: 100%;
	font: 400 calc(100vw / 1440 * 10) / 1.33 $font_1;

	@include bp($point_3) {
		font-size: 10px;
	}

	@include bp(390px) {
		font-size: calc(100vw / 390 * 10);
	}
}

body {
	position: relative;

	display: flex;
	height: 100%;
	// width: 100vw;

	font-size: $font_size_base;
	color: $color_text_base;

	-webkit-text-size-adjust: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background: $body_bg;

	overflow-x: hidden;

	transition: opacity 0.3s;

	@supports (display: grid) {
		display: block;
	}

	&.body--loaded_mod {
		opacity: 1;
	}

	&.body--dark_mod {
		background-color: $black_2;
	}

	&.body--animating_mod,
	&.body--open_menu_state {
		overflow: hidden;
	}

	&.body--scrolled_mod {
		pointer-events: none;
	}
}

body.body--popup_open {
	overflow: hidden;
}

.preloader {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background-color: $white;
	opacity: 1;
	transition: opacity 0.3s;
	z-index: 1000;
	.body--loaded & {
		pointer-events: none;
		opacity: 0;
	}
}

.global_wrapper {
	position: relative;
	width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	height: auto !important;
	overflow-x: hidden;
}

.wrapper {
	display: flex;
	flex-direction: column;
	flex: 1 0 auto;
}

.base {
	width: 100%;
	position: relative;
	padding: 0;
	flex: 1 0 auto;

	// padding-bottom: 3000px !important;
}

a {
	text-decoration: none;
	color: $color_link_base;
	&:hover,
	&:focus {
		text-decoration: underline;
	}
}

// for wp
.editor-styles-wrapper .wp-block {
	margin-bottom: 5rem;
}

html :where(.wp-block) {
	max-width: 1200px;
}

.editor-styles-wrapper {
	overflow-x: hidden;
}

.anim_wrap {
	height: 100vh;
	overflow: hidden;
}

.power,
.extracted,
.list {
	visibility: hidden;
}

//------------------------------------------------------------base styles###
