//------------------------------------------------------------layout
.header {
	position: fixed;
	top: 0;
	left: 0;

	display: flex;
	align-items: center;

	height: var(--height-header);
	width: 100%;
	padding: 1rem var(--gl-indent);

	font-size: 1rem;

	will-change: transform;

	transition: transform 0.3s, background-color 0.3s;

	@include z-index(header);

	&.header--pos_state {
		transform: translateY(-100%);
	}

	&.header--scroll_state {
		background-color: $white;

		.body--dark_mod & {
			background-color: $black;
		}
	}

	.body--dark_mod & {
		background-color: transparent;
	}
}

.header_in {
	display: flex;
	align-items: center;
	width: 100%;
	// max-width: $page_width;
	margin: 0 auto;
}

.header__controls {
	display: flex;
	align-items: center;
	margin-left: auto;
}

.header__btn {
	margin-top: 0.4rem;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-right: 3rem;
		}

		@include bp($point_3) {
			margin-right: 1.6rem;
		}
	}
}

.header__logo {
	position: relative;
	z-index: 1001;

	display: block;
	width: 8.2rem;
	height: 3.6rem;
}

.header__logo_img {
	@include cover_img;
}

.header__logo_text {
	.header--scroll_state & {
		fill: $black;
	}

	.body--dark_mod & {
		fill: $white;
	}

	.body--open_menu_state & {
		@include bp($point_3) {
			fill: $black;
		}
	}
}

.header__logo_bg {
	@include bp($point_3) {
		.body--open_menu_state & {
			fill: $gray;
		}
	}
}

.header__logo_letter {
	@include bp($point_3) {
		.body--open_menu_state & {
			fill: $white;
		}
	}
}

.header__trigger {
	position: relative;
	z-index: 1001;

	margin: 0.4rem 0 0 auto;

	font-size: 1.6rem;
	font-family: $raleway;
	font-weight: 700;
	text-transform: uppercase;
	color: $black;

	transition: color 0.3s;
	cursor: pointer;

	@include bp($point_2, min) {
		padding-right: 5rem;
	}

	@include bp($point_3) {
		padding-right: 4.4rem;
	}

	.header--scroll_state & {
		color: $black;
	}

	.body--dark_mod & {
		color: $white;
	}

	&:hover {
		color: $gray;

		.body--dark_mod & {
			color: $yellow;
		}
	}

	.body--open_menu_state & {
		color: $black;

		&:hover,
		&:focus {
			color: $black;
		}
	}

	&:before,
	&:after {
		content: '';

		position: absolute;
		top: 50%;
		right: 0;

		border-radius: 0.4rem;
		background-color: currentColor;
	}

	&:before {
		height: 0.4rem;
		margin-top: -0.2rem;

		@include bp($point_2, min) {
			width: 3.6rem;
		}

		@include bp($point_3) {
			width: 3rem;
		}
	}

	&:after {
		width: 0.4rem;

		transition: opacity 0.3s;

		@include bp($point_2, min) {
			height: 3.6rem;
			margin-top: -1.8rem;
			margin-right: 1.6rem;
		}

		@include bp($point_3) {
			height: 3rem;
			margin-top: -1.5rem;
			margin-right: 1.4rem;
		}

		.body--open_menu_state & {
			opacity: 0;
		}
	}
}

// menu

.header_menu {
	position: fixed;
	top: 0;
	right: 0;

	z-index: 1000;

	display: flex;
	flex-direction: column;
	height: var(--full-screen);

	background-color: $yellow;
	overflow-y: auto;

	opacity: 0;
	pointer-events: none;
	transform: translateX(100%);

	transition: all 0.3s;

	@include bp($point_2, min) {
		width: 68.2rem;
		padding: min(14rem, 13vh) 5.4rem 6rem;
	}

	@include bp($point_3) {
		width: 100%;
		padding: 7.8rem 3.2rem 3rem;
	}

	.body--open_menu_state & {
		opacity: 1;
		transform: translateX(0);
		pointer-events: initial;
	}
}

.header_menu__list {
	@include bp($point_2, min) {
		margin-bottom: 4rem;
	}

	@include bp($point_3) {
		margin-bottom: 9.4rem;
	}
}

.header_menu__item {
	display: flex;
	justify-content: center;
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.4rem;
		}
	}
}

.header_menu__link {
	@include h1;

	display: block;
	padding: 1.2rem 1rem;

	line-height: 1;
	text-align: center;

	color: $black_4;

	transition: all 0.3s;

	@include bp($point_2, min) {
		min-width: 37rem;
	}

	@include bp($point_3) {
		width: 100%;
	}

	&:hover,
	&:focus {
		color: $yellow;
		background-color: $gray;
		text-decoration: none;
	}
}

.header_menu__btn {
	@include bp($point_3) {
		display: flex;
		justify-content: center;
		margin-top: auto;
	}
}

.header_menu__contact {
	@include bp($point_2, min) {
		margin: auto 0 2.8rem;
	}
	@include bp($point_3, $point_4) {
		text-align: center;

		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.header_menu__contact_title {
	font-size: 1.6rem;
	font-family: $raleway;
	font-weight: 700;
	text-transform: uppercase;
	color: #070707;
}

.header_menu__contact_link {
	font-size: 4rem;
	color: $black;
	opacity: 0.7;
	text-decoration: underline;
}

//------------------------------------------------------------layout###
