@import 'ScssHelpers/helpers-all.scss';

.hero {
	padding: 9.6rem var(--gl-indent) 0;

	@include bp($point_3) {
		display: flex;
		min-height: 100vh;
	}

	.section_in {
		@include bp($point_3) {
			display: flex;
			flex-direction: column;
		}
	}
}

.hero__title {
	@include h1;

	position: relative;

	max-width: 80rem;
	margin-bottom: 2.4rem;

	@include bp($point_4 - 1) {
		padding-right: 3rem;
	}
}

.hero__title_icon {
	position: absolute;
	top: 0;
	right: 100%;

	width: 5.6rem;
	height: 4rem;

	margin: 3rem 1.4rem 0 0;

	@include bp($point_3) {
		display: none;
	}
}

.hero__content {
	position: relative;
	// overflow: hidden;

	@include bp($point_3) {
		flex-grow: 1;
	}
}

.hero__text {
	display: flex;
	position: relative;
	z-index: 3;

	padding-top: 1.8rem;

	&:after {
		content: '';
		flex-grow: 1;
		margin-left: -1px;

		background-color: $white;
	}
}

.hero__text_bg {
	position: absolute;

	background-color: $white;

	&:nth-child(1) {
		top: 0;
		left: 1px;
		width: 50vw;
		height: calc(100% + 1px);
		margin-left: -50vw;
	}

	&:nth-child(2) {
		top: 0;
		left: calc(100% - 1px);
		width: 50vw;
		height: 100%;
	}

	&:nth-child(3) {
		top: -1px;
		left: 50%;
		width: 150vw;
		height: calc(2rem + 2px);
		margin-left: -50vw;
	}

	&:nth-child(4) {
		top: calc(100% - 1px);
		left: 50%;
		width: 150vw;
		height: 3.6rem;
		margin-left: -50vw;
	}
}

.hero__text_svg {
	display: block;
	width: 68.4rem;
	max-width: 100%;
	flex-shrink: 0;

	@include bp($point_3) {
		width: 29.4rem;
	}
}

.hero__content {
	@include bp($point_2, min) {
		min-height: 33.15vw;
	}

	@include bp($point_3) {
		min-height: 66vw;
	}
}

.hero__video {
	position: absolute;
	top: 0;
	left: 50%;
	width: 100vw;
	height: 100%;
	margin-left: -50vw;
}

.hero__video_poster {
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	z-index: 2;

	pointer-events: none;
	transition: opacity 0.6s;

	&.hero__video_poster--loaded_mod {
		opacity: 0;
	}
}

.hero__video_poster_img {
	@include cover_img;
}

.hero__video_el {
	position: absolute;
	top: 0;
	left: 0;

	@include cover_img;
}
